import React from "react";
import CheckIcon from "@material-ui/icons/Check";

function McqQuestionDisplay(props) {
  return (
    <div
      onClick={props.onClick}
      style={{
        width: props.width ? props.width : "auto",
        background: props.isCorrect ? "#d2e0df" : "#f3d6d8",
        margin: "8px",
        padding: "12px",
        borderRadius: "5px",
        fontSize: "11px",
        fontWeight: "500",
      }}
    >
      <div dangerouslySetInnerHTML={{ __html: props.mcqText }}></div>
      <div>
        {props.mcqOptions &&
          props.mcqOptions.map((option, index) => {
            return (
              <div
                style={{
                  display: "flex",
                  padding: "5px 2px",
                }}
              >
                <div className="sno"> {index + 1}.&nbsp;</div>
                <div className="option-text">{option.text} </div>
                {option.correct ? (
                  <div style={{ marginLeft: "10px" }}>
                    {" "}
                    <CheckIcon
                      style={{
                        color: props.selected ? "#fff" : "#04724d",
                        height: "20px",
                        width: "20px",
                      }}
                    />{" "}
                  </div>
                ) : null}
                {option.text === props.chosenOption && !props.isCorrect ? (
                  <div style={{ marginLeft: "10px" }}>
                    {" "}
                    <CheckIcon
                      style={{
                        color: props.selected ? "#fff" : "red",
                        height: "20px",
                        width: "20px",
                      }}
                    />{" "}
                  </div>
                ) : null}
              </div>
            );
          })}
      </div>
    </div>
  );
}

export default McqQuestionDisplay;
