  componentDidMount() {}

  handleRangeFrom = (e) => {
    this.setState({
      rangeFrom: e.target.value,
    });
  };

  handleRangeTo = (e) => {
    this.setState({
      rangeTo: e.target.value,
    });
  };

  handleSelect = (e, item) => {
    let allQuestions = this.state.allQuestions;
    allQuestions.map((item2) => {
      if (item2.id == item.id) {
        item2.score = e.value;
      }
    });

    this.setState({
      allQuestions: allQuestions,
    });
  };

  handleSelect2 = (e, item) => {
    let allQuestions2 = this.state.allQuestions2;
    allQuestions2.map((item2) => {
      if (item2.id == item.id) {
        item2.score = e.value;
      }
    });

    this.setState({
      allQuestions2: allQuestions2,
    });
  };

  handleFlowChange = (item, e) => {
    let oldValue;
    let tableData = this.state.tableData;
    tableData.map((item2) => {
      if (item2.id == item.id) {
        oldValue = item2.flow;
        item2.flow = e.value;
      }
    });

    tableData.map((item3) => {
      if (item3.flow == e.value && item3.id != item.id) {
        item3.flow = oldValue;
      }
    });

    this.setState({
      tableData: tableData,
    });
  };

  handleWeightageChange = (item, e) => {
    let tableData = this.state.tableData;
    tableData.map((item2) => {
      if (item2.id == item.id) {
        item2.weightage = e.target.value;
      }
    });

    this.setState({
      tableData: tableData,
    });
  };

  handleSkipChange = (item, e) => {
    let tableData = this.state.tableData;
    tableData.map((item2) => {
      if (item2.id == item.id) {
        item2.skip = !item2.skip;
      }
    });

    this.setState({
      tableData: tableData,
    });
  };

  handleRetakeChange = (item, e) => {
    let tableData = this.state.tableData;
    tableData.map((item2) => {
      if (item2.id == item.id) {
        item2.retake = !item2.retake;
      }
    });

    this.setState({
      tableData: tableData,
    });
  };

