import React from "react";
import SimpleRating from "../Rating/SimpleRating";
import "./verbalQuestionsDisplay.scss";
import ScreenRecording from "../../assets/images/ScreenRecording.svg";
import ShowVideo from "../../assets/images/ShowVideo.svg";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";
import { styled } from "@mui/material/styles";
const LightTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(() => ({
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: "#fff",
    color: "#000000",
    fontSize: 12,
    fontWeight: "600",
  },
}));
function VerbalQuestionDisplay(props) {
  return (
    <div>
      {props.verbalQuestions.map((item, index) => {
        return (
          <div className="verbal-body-container" key={index}>
            <div className="verbal-body-question">{item.question}</div>
            <div style={{ margin: "7px 10px 7px 0px", cursor: "pointer" }}>
              <LightTooltip title="Screen Recording" placement="top">
                <img
                  src={ScreenRecording}
                  alt=""
                  onClick={() => props.onClick(item.screen)}
                />
              </LightTooltip>
            </div>
            <div style={{ cursor: "pointer", margin: "7px 0px" }}>
              <LightTooltip title="Video" placement="top">
                <img
                  src={ShowVideo}
                  alt=""
                  onClick={() => props.onClick(item.video)}
                />
              </LightTooltip>
            </div>
            <div className="verbal-body">{item.time}</div>
            {/* <div className="verbal-body-rating">
              <SimpleRating value={parseInt(item.rating)} />
            </div> */}

            <div className="verbal-body">{item.score}</div>
          </div>
        );
      })}
    </div>
  );
}

export default VerbalQuestionDisplay;
