import React from "react";
import "./questionType.scss";
import ControlledCheckbox from "../Atoms/Checkbox";

const QuestionType = (props) => {
  return (
    <div className="main-questionType">
      <div className="select-questions">Select Question Type</div>

      {props.questionTypes.map((item) => {
        return (
          <div className="question-type">
            <ControlledCheckbox
              checked={item.checked}
              labelStyle={{
                marginLeft: 15,
                userSelect: "none",
                fontSize: "12px",
                fontWeight: "500",
              }}
              size={20}
              borderRadius={2}
              borderColor={"#04724d"}
              label={item.label}
              onChange={() => {
                props.handleChange(item);
              }}
            />
          </div>
        );
      })}
    </div>
  );
};

export default QuestionType;
