import * as React from "react";
import { styled, alpha } from "@mui/material/styles";
import Button from "@mui/material/Button";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import "./customTotalScoreSelect.scss";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import ControlledRadio from "../Radio";
import Divider from "@mui/material/Divider";

const StyledMenu = styled((props) => (
  <Menu
    elevation={0}
    anchorOrigin={{
      vertical: "bottom",
      horizontal: "right",
    }}
    transformOrigin={{
      vertical: "top",
      horizontal: "right",
    }}
    {...props}
  />
))(() => ({
  "& .MuiPaper-root": {
    borderRadius: 4,
    marginTop: 6,
    minWidth: 210,

    "& .MuiMenu-list": {
      padding: "20px 10px",
      border: "1px solid #CCCCCC",
      display: "flex",
      flexDirection: "column",
    },
    "& .MuiMenuItem-root": {
      "& .MuiSvgIcon-root": {
        fontSize: 11,
      },
      "&:active": {
        backgroundColor: "#fff",
        color: "#fff",
      },
    },
  },
}));

export default function CustomTotalScoreSelect(props) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div>
      <Button
        id="demo-customized-button"
        aria-controls={open ? "demo-customized-menu" : undefined}
        aria-haspopup="true"
        aria-expanded={open ? "true" : undefined}
        variant="contained"
        className="custom-select-button"
        disableElevation
        onClick={handleClick}
        endIcon={
          <ArrowDropDownIcon style={{ height: "30px", width: "30px" }} />
        }
      >
        {props.selectName}
      </Button>
      <StyledMenu
        id="demo-customized-menu"
        MenuListProps={{
          "aria-labelledby": "demo-customized-button",
        }}
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        className="main-menu"
      >
        {props.percentagesOptions ? (
          <MenuItem disableRipple onClick={handleClose}>
            <div className="main-filter-div">
              <div className="filter-heading">PERCENTAGE</div>
              <div className="filter-content">
                <div className="radio-fiex">
                  <ControlledRadio
                    radioOptions={props.percentagesOptions}
                    radioValue={props.totalScoreValue}
                    handleChangeRadio={(e) => {
                      props.handlePercentagesChangeRadio(e);
                    }}
                  />
                </div>
              </div>
            </div>
          </MenuItem>
        ) : null}
        {props.countOptions ? (
          <MenuItem disableRipple onClick={handleClose}>
            <div className="main-filter-div">
              <div className="filter-heading">COUNT</div>
              <div className="filter-content">
                <div className="radio-fiex">
                  <ControlledRadio
                    radioOptions={props.countOptions}
                    radioValue={props.totalScoreValue}
                    handleChangeRadio={(e) => {
                      props.handleCountChangeRadio(e);
                    }}
                  />
                </div>
              </div>
            </div>
          </MenuItem>
        ) : null}

        <MenuItem disableRipple >
          <div className="main-filter-div">
            <div className="filter-heading">SCORE RANGE</div>
            <div className="filter-content">
              <div className="radio-fiex">
                <ControlledRadio
                  radioOptions={props.customOptions}
                  radioValue={props.totalScoreValue}
                  handleChangeRadio={(e) => {
                    props.handleCustomChangeRadio(e);
                  }}
                />

                {props.totalScoreValue == "Custom Range" ? (
                  <div>
                  <div className="range-div">
                    <div className="range-div-1">
                      <input
                        type="number"
                        name="experienceFrom"
                        className="range-input"
                        onChange={props.handleRangeFrom}
                        placeholder="0"
                        value={props.rangeFrom}
                      />
                    </div>

                    <div className="range-div-2"> To </div>

                    <div className="range-div-3">
                      <input
                        type="number"
                        name="experienceTo"
                        className="range-input"
                        onChange={props.handleRangeTo}
                        placeholder="0"
                        value={props.rangeTo}
                      />
                    </div>
                
                  </div>
                  <div className="update-button-div"> 
                    <Button
        id="demo-customized-button"
        variant="contained"
        className="update-button"
        disableElevation
        onClick={async()=>{
         await props.handleUpdate()
          handleClose()
        }}
      
      > Update </Button>
                    </div>
                  </div>
                ) : null}
              </div>
            </div>
          </div>
        </MenuItem>

        <Divider sx={{ my: 0.5 }} />
        <div className="clear-selection-div">
          <div className="clear-all" onClick={async()=>{
          await  props.clearSelectionHandler()
          handleClose()
          }}>
            Clear Selection
          </div>
          <div className="total-of-total">
            {(props.countOptions && props.countOptions.length
              ? props.countOptions.length
              : 0) +
              (props.percentagesOptions && props.percentagesOptions.length
                ? props.percentagesOptions.length
                : 0) +
              (props.customOptions && props.customOptions.length
                ? props.customOptions.length
                : 0)}{" "}
            OF{" "}
            {(props.countOptions && props.countOptions.length
              ? props.countOptions.length
              : 0) +
              (props.percentagesOptions && props.percentagesOptions.length
                ? props.percentagesOptions.length
                : 0) +
              (props.customOptions && props.customOptions.length
                ? props.customOptions.length
                : 0)}
          </div>
        </div>
      </StyledMenu>
    </div>
  );
}
