import React, { useState } from "react";
import "./card.css";
import moment from "moment";
import "react-date-range/dist/styles.css"; // main css file
import "react-date-range/dist/theme/default.css"; // theme css file
import { DateRangePicker, Calendar } from "react-date-range";
import { Fragment } from "react";
import InsertInvitationIcon from "@material-ui/icons/InsertInvitation";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
export default function TableDatePicker(props) {
  const tomorrow = new Date(props.startDate);
  const [open, setOpen] = useState(false);
  tomorrow.setDate(tomorrow.getDate() + 1);
  const selectionRange = {
    startDate: new Date(props.startDate),
    endDate: new Date(props.endDate),
    key: "selection",
  };
  const handleSelect = (ranges) => {
    props.handleSelectDate(ranges);
    setOpen(!open);
  };
  return (
    <ClickAwayListener
      onClickAway={() => {
        setOpen(false);
      }}
    >
      {/* {props.isSingleDate ? (
        <Fragment>
          <div
              style={{
                display: "flex",
                justifyContent: "space-between",
                // marginTop: "-7px",
                padding:'3px'
              }}
              className="calendar"
            >
              <div className="fieldName">
                {moment(props.date).format("DD-MMM-YYYY")}
              </div>
              <div style={{marginRight:'10px'}}>
                <InsertInvitationIcon style={{color:'#757575'}}/>
              </div>
          </div>
          <div style={{ position: "absolute", zIndex: "60" }} id="jdh">
            {open && (
              <Calendar onChange={(e) => handleSelect(e)} color={"#28724d"} />
            )}
          </div>
            
        </Fragment>
      ) : ( */}
      <div>
        <fieldset className="calendar" onClick={() => setOpen(!open)}>
          <legend
            className={props.legendClass ? props.legendClass : "legend-wrapper"}
          >
            {props.legend ? props.legend : "Date"}
          </legend>
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              paddingRight: "10px",
              paddingLeft: "10px",
              marginTop: "-7px",
            }}
          >
            <div className="fieldName">
              {moment(props.startDate).format("DD-MMM-YYYY")} to{" "}
              {moment(props.endDate).format("DD-MMM-YYYY")}
            </div>
            <div>
              <InsertInvitationIcon
                style={{ color: "#757575", paddingBottom: "6px" }}
              />
            </div>
          </div>
        </fieldset>
        {/* <div style={{ position: "absolute", zIndex: "60" }} > */}
        {open && (
          <DateRangePicker
            ranges={[selectionRange]}
            rangeColors={["#28724d"]}
            color={"#28724d"}
            onChange={(e) => handleSelect(e)}
          />
        )}
        {/* </div> */}
      </div>
    </ClickAwayListener>
  );
}
